@mixin for-tablet-only {
	@media (max-width: 1024px) {
		@content;
	}
}

@mixin for-mobile-only {
	@media (max-width: 767px) {
		@content;
	}
}

@mixin adaptive-mobile-point {
	@media (max-width: 455px) {
		@content;
	}
}

@mixin for-small-mobile-only {
	@media (max-width: 374px) {
		@content;
	}
}
